function Portfolio(props) {
  const scrollRefs = useRef({});
  const handleMouseEnter = (event, id) => {
    const img = event.currentTarget.querySelector('img');
    const imgHeight = img.scrollHeight;
    const containerHeight = event.currentTarget.clientHeight;
    const maxScroll = imgHeight - containerHeight;

    if (maxScroll > 0) {
      const speed = 100;
      const step = speed / 60;

      let currentScroll = 0;

      const animate = () => {
        currentScroll += step;
        if (currentScroll >= maxScroll) {
          currentScroll = maxScroll;
        }
        img.style.transform = `translateY(-${currentScroll}px)`;

        if (currentScroll < maxScroll) {
          scrollRefs.current[id] = requestAnimationFrame(animate);
        }
      };

      scrollRefs.current[id] = requestAnimationFrame(animate);
    }
  };

  const handleMouseLeave = (event, id) => {
    const img = event.currentTarget.querySelector('img');
    cancelAnimationFrame(scrollRefs.current[id]);
    img.style.transform = 'translateY(0)';
  };

  return (
    <section id="portfolio">
      <div className="portfolio_box">
        <ul className="portfolio_list">
          {props.port.map((a, i) => (
            <li key={props.port[i].id}>
              <div className="box inner">
                <div 
                  className="thum img_100" 
                  onMouseEnter={(e) => handleMouseEnter(e, a.id)} 
                  onMouseLeave={(e) => handleMouseLeave(e, a.id)}
                  onClick={() => {
                    props.setPortview(true);
                    props.setSelectedPort(a);
                  }}
                >
                  <img src={"/img/portfolio/" + props.port[i].img} alt="" />
                </div>
                <ul className="cont">
                  <li className='name'>{props.port[i].name}</li>
                  <li><b></b>{props.port[i].long}</li>
                  <li><b></b>{props.port[i].join}</li>
                  <li><b></b><div dangerouslySetInnerHTML={{ __html: props.port[i].content }}></div></li>
                  {props.port[i].url ? (
                    <li className="link">
                      <a href={props.port[i].url} target="_blank" rel="noopener noreferrer">
                        <span>  </span><i className="xi-external-link"></i>
                      </a>
                    </li>
                  ) : null}
                </ul>
              </div>
            </li>
          ))}
        </ul>
      </div>
    </section>
  );
}

function PortfolioView(props) {

  const viewRef = useRef(null);

  useEffect(() => {
    if (viewRef.current) {
      viewRef.current.scrollTo({ top: 0});
    }
  }, [props.port]);

  if (!props.port) {
    return <div>   .</div>;
  }

  return (
    <section id="portfolioview" ref={viewRef}>
      <div className="portfolioview_box ">
        <div className="thum img_100"><img src={"/img/portfolio/" + props.port.img} alt={props.port.name} /></div>
      </div>
    </section>
  );
}

