import React, { useState } from 'react';
import emailjs from 'emailjs-com';

const ContactForm = () => {
  const [formData, setFormData] = useState({
    from_name: '',
    email: '',
    message: ''
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    emailjs.send('service_lerd2nt', 'template_o32okyf', formData, 'eUj2gowei543KlPRE')
      .then((response) => {
        console.log('SUCCESS!', response.status, response.text);
        alert('!     .');
      }, (error) => {
        console.log('FAILED...', error);
        alert(' .');
      });
  };

  return (
    <article id="form_box">
        <div className="inner">
            <div className="form_top">
                <div className="tit"></div>
                <div className="desc">
                             ? <br />
                           :) <br />
                        .
                </div>
            </div>
        </div>
        <form onSubmit={handleSubmit}>
            <ul className="form_list">
                <li>
                    <label htmlFor="form_name" className="tit"></label>
                    <div className="cont">
                        <input id="form_name" type="text" name="from_name" value={formData.from_name} onChange={handleChange} placeholder="" required />
                    </div>
                </li>
                <li>
                    <label htmlFor="form_email" className="tit">   or </label>
                    <div className="cont">
                        <input id="form_email" type="text" name="email" value={formData.email} onChange={handleChange} placeholder="   or " required />
                    </div>
                </li>
                <li>
                    <label htmlFor="form_message" className="tit"></label>
                    <div className="cont">
                        <textarea id="form_message" name="message" value={formData.message} onChange={handleChange} placeholder="" required></textarea>
                    </div>
                </li>
            </ul>
            <div className="form_btn"><button type="submit"></button></div>
        </form>
    </article>
  );
};

export default ContactForm;
